const fetchBookRequest = () => {
  return {
    type: 'FETCH_BOOK_REQUEST'
  }
}
const fetchBookLoad = (books) => {
  return {
    type: 'FETCH_BOOK_LOAD',
    payload: books
  }
}

const fetchBookError = (error) => {
  return {
    type: 'FETCH_BOOK_ERROR',
    payload: error
  }
}

const addBookToCart = (bookId) => {
  return {
    type: 'ADD_BOOK_TO_CART',
    payload: bookId
  }
}

const deleteBookItemFromCart = (bookId) => {
  return {
    type: 'DELETE_BOOK_ITEM_FROM_CART',
    payload: bookId
  }
}

const deleteAllBooksFromCart = (bookId) => {
  return {
    type: 'DELETE_ALL_BOOKS_FROM_CART',
    payload: bookId
  }
}

const fetchBooksOld = (bookstoreService, dispatch) => {
  dispatch(fetchBookRequest()) ;
  bookstoreService.getBooks()
  .then(books => {
    dispatch(fetchBookLoad(books))
  })
  .catch(err => {
    dispatch(fetchBookError(err))
    console.error(err)})
}

const fetchBooks = (bookstoreService) => () => (dispatch) => {
  dispatch(fetchBookRequest()) ;
  bookstoreService.getBooks()
  .then(books => {
    dispatch(fetchBookLoad(books))
  })
  .catch(err => {
    dispatch(fetchBookError(err))
    console.error(err)})
}



export {
  addBookToCart,
  deleteBookItemFromCart,
  deleteAllBooksFromCart,
  fetchBooks}