import React from 'react'
import BookListItem from '../book-list-item'
import Spinner from '../spinner'
import {withBookstoreService} from '../hoc'
import {connect} from 'react-redux'
import { addBookToCart, fetchBooks } from '../../actions'
import ErrorIndicator from '../error-indicator/error-indicator';
import { bindActionCreators } from 'C:/Users/tarst/AppData/Local/Microsoft/TypeScript/3.5/node_modules/redux';

const BookList = ({books, addBookToCart}) => {
  const booksItems = books.map(book => {
    return <BookListItem book={book} key={book.id} onAddBookToCart={() => addBookToCart(book.id)} />
  })
  return (
    <ul className='list-group'>
      {booksItems}
    </ul>
  )
}

class BookListContainer extends React.Component {
  componentDidMount() {
   this.props.fetchBooks()
  }
  

  render() {
    const { books, loading, error, addBookToCart } = this.props;

    if (error) {
      return <ErrorIndicator/>
    }
    if (loading) {
      return <Spinner/>
    }

    return (
     <BookList books={books} addBookToCart={addBookToCart} />
    )
  }
}

const mapStateToProps = ({bookList: {books, error, loading}}) => {
  return {
    books,
    error,
    loading
  }
}

const mapDispatchToProps = (dispatch, ownPropery) => {
  return bindActionCreators({
    fetchBooks: fetchBooks(ownPropery.bookstoreService),
    addBookToCart:  addBookToCart
  }, dispatch)
}

export default  withBookstoreService(connect(mapStateToProps, mapDispatchToProps)(BookListContainer)) 


