const initialState = {
  bookList: {
    books: [],
    error: null,
    loading: true
  },

  shoppingCart: {
    cartItems: []
  }
}


const updateCartItems = (cartItems, item, idx) => {

  if (item.count === 0) {
    return [
      ...cartItems.slice(0, idx),
      ...cartItems.slice(idx + 1)
    ]
  }
  if (idx > -1) {
    return [
      ...cartItems.slice(0, idx),
      item,
      ...cartItems.slice(idx + 1)
    ]
  }

  return [
    ...cartItems,
    item
  ]
}

const updateCartItem = (item = {}, book, quantity) => {
  const {
    id = book.id,
    title = book.title,
    price = 0,
    count = 0
  } = item;

  return {
    id,
    title,
    price: book.price * quantity + price,
    count: count + quantity
  }
}

const updateOrder = (state, bookId, quantity) => {
  const book = state.bookList.books.find(book => book.id === bookId);
  const idx = state.shoppingCart.cartItems.findIndex(book => book.id === bookId);
  const item = state.shoppingCart.cartItems[idx];
  let newBook = updateCartItem(item, book, quantity)
  return {
    cartItems: updateCartItems(state.shoppingCart.cartItems, newBook, idx),
  }
}
const updateBookList = (state, action) => {
switch (action.type) {
  case 'FETCH_BOOK_REQUEST':
      return {
        books: [],
        error: null,
        loading: true
      };
    case 'FETCH_BOOK_ERROR':
      return {
        books: [],
        error: action.payload,
        loading: false
      };
    case 'FETCH_BOOK_LOAD':
      return {
        books: action.payload,
        error: null,
        loading: false
      };


  default:
    return state.bookList
    break;
}
}

const updateShoppingCart = (state, action) => {
  switch (action.type) {
    case 'ADD_BOOK_TO_CART':
      return updateOrder(state, action.payload, 1);

    case 'DELETE_BOOK_ITEM_FROM_CART':
      return updateOrder(state, action.payload, -1);

    case 'DELETE_ALL_BOOKS_FROM_CART':
      const item = state.shoppingCart.cartItems.find(({ id }) => id === action.payload)
      return updateOrder(state, action.payload, -item.count);

    default:
      return state.shoppingCart;
  }
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'FETCH_BOOK_REQUEST':
    case 'FETCH_BOOK_ERROR':
    case 'FETCH_BOOK_LOAD':
      return {
        ...state,
        bookList: updateBookList(state, action)
      }
    case 'ADD_BOOK_TO_CART':
    case 'DELETE_BOOK_ITEM_FROM_CART':
    case 'DELETE_ALL_BOOKS_FROM_CART':

      return {
        ...state,
        shoppingCart: updateShoppingCart(state, action)
      }

    default:
      return state
  }
}

export default reducer